'use client';

import { Vehicle, MaintenanceStatus } from '@/types';
import { GlassCard } from '@/components/ui/GlassCard';
import { Reveal } from '@/components/ui/Reveal';
import { Badge } from '@/components/ui/badge';
import { Progress } from '@/components/ui/progress';
import {
  Car,
  Ambulance,
  Helicopter,
  AlertTriangle,
  CheckCircle,
  Clock,
  MapPin,
  Users
} from 'lucide-react';
import Link from 'next/link';

interface VehicleCardProps {
  vehicle: Vehicle;
}

export function VehicleCard({ vehicle }: VehicleCardProps) {
  // Calculate maintenance status summary
  const deviceStats = vehicle.devices.reduce((acc, device) => {
    acc[device.status] = (acc[device.status] || 0) + 1;
    return acc;
  }, {} as Record<MaintenanceStatus, number>);

  const totalDevices = vehicle.devices.length;
  const overdueCount = deviceStats.overdue || 0;
  const dueCount = deviceStats.due || 0;
  const upcomingCount = deviceStats.upcoming || 0;
  const completedCount = deviceStats.completed || 0;

  const healthPercentage = totalDevices > 0 ? Math.round(((totalDevices - overdueCount - dueCount) / totalDevices) * 100) : 100;

  const getStatusColor = (status: Vehicle['status']) => {
    switch (status) {
      case 'active': return 'bg-emerald-500/20 text-emerald-300 border-emerald-400/30';
      case 'maintenance': return 'bg-amber-500/20 text-amber-300 border-amber-400/30';
      case 'out_of_service': return 'bg-red-500/20 text-red-300 border-red-400/30';
      default: return 'bg-slate-500/20 text-slate-300 border-slate-400/30';
    }
  };

  const getVehicleIcon = (type: Vehicle['type']) => {
    switch (type) {
      case 'car': return <Car className="h-6 w-6" />;
      case 'ambulance': return <Ambulance className="h-6 w-6" />;
      case 'helicopter': return <Helicopter className="h-6 w-6" />;
    }
  };

  const getHealthColor = (percentage: number) => {
    if (percentage >= 80) return 'text-emerald-400';
    if (percentage >= 60) return 'text-amber-400';
    return 'text-red-400';
  };

  return (
    <Reveal>
      <Link href={`/vehicle/${vehicle.id}`} className="block h-full">
        <GlassCard hoverEffect className="h-full flex flex-col justify-between group">
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-3">
                <div className="p-2 rounded-xl bg-gradient-to-br from-blue-500/30 via-cyan-400/20 to-emerald-400/20 text-cyan-100 shadow-lg shadow-blue-500/20 group-hover:scale-110 transition-transform duration-300">
                  {getVehicleIcon(vehicle.type)}
                </div>
                <div>
                  <h3 className="text-lg font-semibold text-white tracking-tight">{vehicle.name}</h3>
                  <p className="text-sm text-gray-400">{vehicle.model}</p>
                </div>
              </div>
              <Badge className={`${getStatusColor(vehicle.status)} border backdrop-blur-md`}>
                {vehicle.status.replace('_', ' ')}
              </Badge>
            </div>

            <div className="space-y-2">
              <div className="flex items-center justify-between text-sm text-gray-300">
                <span className="font-medium">Fleet Health</span>
                <span className={`font-semibold ${getHealthColor(healthPercentage)}`}>
                  {healthPercentage}%
                </span>
              </div>
              <Progress value={healthPercentage} className="h-1.5 bg-gray-800" />
            </div>

            <div className="grid grid-cols-2 gap-3 text-sm">
              <div className="p-2 rounded-lg bg-white/5 flex items-center gap-2 text-red-300 border border-white/5">
                <AlertTriangle className="h-4 w-4" />
                <span>{overdueCount} Overdue</span>
              </div>
              <div className="p-2 rounded-lg bg-white/5 flex items-center gap-2 text-amber-300 border border-white/5">
                <Clock className="h-4 w-4" />
                <span>{dueCount} Due Soon</span>
              </div>
            </div>
          </div>

          <div className="mt-4 pt-4 border-t border-white/10 space-y-2 text-sm text-gray-400">
            <div className="flex items-center gap-2">
              <MapPin className="h-3.5 w-3.5" />
              <span className="truncate">{vehicle.location}</span>
            </div>
            <div className="flex items-center gap-2">
              <Users className="h-3.5 w-3.5" />
              <span>{vehicle.assignedContacts.length} contacts</span>
            </div>
          </div>
        </GlassCard>
      </Link>
    </Reveal>
  );
}
